.video_area{
    padding-bottom: 60px;
    padding-top: 150px;
    @media #{$mobile_device}{
        padding-top: 50px;
    }
    .video_wrap{
        .thumb{
            margin-bottom: 70px;
            position: relative;
            img{
                width: 100%;
                border: 1px solid $steady-color;
            }
            .video_icon a {
                width: 70px;
                height: 70px;
                background: $highlight-color;
                display: inline-block;
                text-align: center;
                line-height: 70px;
                border-radius: 50%;
                color: #fff;
                position: absolute;
                top: 50%;
                left: 50%;
                @include transform(translate(-50%,-50%));
                @media #{$mobile_device} {
                    width: 50px;
                    height: 50px;
                    line-height: 47px;
                }
                &:hover{
                    background: darken($color: $highlight-color, $amount: 10%);
                }
                i{
                    font-size: 16px;
                    @media #{$mobile_device} {
                        font-size: 13px;
                    }
                }
            
            }
        }
        .video_text{
            p{
                font-size: $font-md;
                font-weight: 600;
                line-height: 1.3;
                margin-top: 5px;
                color: #001D38;
                @media #{$mobile_device} {
                    font-size: $font-sm; 
                }
                br{
                    @media (min-width: 320px) and (max-width: 991px) {
                        display: none;
                    }
                }
            }
        }
    }   
}