.content-box {
    background: url(../images/content-box-bg.svg) no-repeat left top;
    background-size: cover;
    margin-top: 30px;
    margin-bottom: 100px;
    padding: 0 10% 50px 10%;
    position: relative;
    .content-box-bg {
        background: $white-color;
    }
    .content-box-pic {
        max-width: 400px;
    }
    @media #{$tab} {
        background-size: 100%;
        padding: 70px 10% 50px 10%;
        margin-top: 0;
        margin-bottom: 50px;
    }
    @media #{$small_mobile} {
        padding: 40px 10% 50px 10%;
    }
    &-page {
        padding: 40px 6% 40px 6%;
        margin-top: 0;
        margin-bottom: 50px;
        position: relative;
        .content-box-pic {
            max-width: 400px;
            margin-top: -50px;
        }
        @media #{$tab} {
            padding: 50px 6% 30px 6%;
            .top-title {
                text-align: left;
                img {
                    max-width: 350px;
                }
                .top-title-txt {
                    font-size: $font-xs;
                }
            }
            .content-box-pic {
                max-width: 250px;
                position: absolute;
                right: 0;
                top: -90px;
            }
        }
        @media #{$large_mobile} {
            margin-bottom: 30px;
            .top-title {
                .top-title-txt {
                    font-size: $font-xxs;
                }
            }
            .content-box-pic {
                max-width: 180px;
                top: -30px;
            }
        }
        @media #{$xs_mobile} {
            padding: 60px 6% 40px 6%;
            .top-title {
                margin-top: 10px;
            }
            .content-box-pic {
                max-width: 160px;
                top: -140px;
                right: calc(50% - 80px);
            }
        }
    }
    &-bottom {
        background: $white-color;
    }
}
.top-title {
    font-weight: $font-heavy;
    color: $gray-dark-color;
    @media #{$tab_device} {
        text-align: center;
    }
    img {
        max-width: 450px;
        @media #{$tab_device} {
            max-width: 100%;
        }
    }
    .top-title-txt {
        @include theme-color;
        color: $white_color;
        font-weight: $font-light;
        padding: 3px 20px 5px 20px;
    }
}
.top-subtitle {
    color: $secondary-color;
    font-size: $font-xl;
    font-weight: $font-light;
}
.title {
    font-size: $font-lg;
    color: $secondary-color;
    margin-bottom: 15px;
    @media #{$xs_mobile} {
        font-size: $font-md;
    }
}
.subtitle {
    font-size: $font-md;
    color: $base-color;
}
.form {
    .form-icon {
        width: 33px;
        position: absolute;
        left: 10px;
        top: 3px;
    }
}
.notice-title {
    background: $base-color;
    color: $white_color;
    display: inline-block;
    padding: 3px 15px;
    font-size: $font-sm;
}
.table-rwd {
    min-width: 100%;
    border: 0;
    tr {
        @media #{$tab} {
            border-bottom: 15px solid $white-color !important;
            background: lighten($color: $gray-light-color, $amount: 20%) !important;
            border: 0;
        }
    }
    th {
        display: table-cell;
        @media #{$tab} {
            display: none;
        }
    }
    td {
        display: table-cell;
        @media #{$tab} {
            text-align: left;
            display: block;
            border: 0;
            padding: 10px 20px 10px 20px;
            position: relative;
            &:after {
                content: "";
                display: block;
                position: absolute;
                width: calc(100% - 40px);
                height: 1px;
                left: 20px;
                bottom: 0;
                background: $gray-light-color;
            }
            &:last-child {
                &:after {
                    display: none!important;
                }
            }
        }
        &:before {
            display: none;
            @media #{$tab} {
                content: attr(data-th);
                font-weight: $font-heavy;
                display: block;
                width: 100%;
                margin-right: calc(100% - 90px);
                color: $steady-color;
                font-size: $font-xs;
            }
        }
    }
}
.table-simple {
    th {
        background: $steady-color;
        color: $white_color;
        font-weight: $font-light;
        font-size: $font-md;
    }
}
.list {
    .list-item {
        margin-bottom: 15px;
        @media #{$xs_mobile} {
            flex-wrap: wrap;
        }
    }
    .list-title {
        display: block;
        background: $base-color;
        color: $white_color;
        width: 128px;
        padding: 2px 13px;
        @media #{$xs_mobile} {
            width: auto;
            display: inline-block;
        }
    }
    .list-txt {
        width: calc(100% - 140px);
        margin-bottom: 0;
        @media #{$xs_mobile} {
            width: 100%;
        }
    }
}
.info-box {
    margin-bottom: 10px;
    display: flex;
    background: $white-color;
    border: 1px solid $base-color;
    @media #{$xs_mobile} {
        flex-wrap: wrap;
    }
    .info-box-title {
        width: 115px;
        padding: 5px 15px;
        background: $base-color;
        color: $white_color;
        text-align: right;
        @media #{$xs_mobile} {
            width: 100%;
            text-align: left;
        }
    }
    .info-box-txt {
        width: calc(100% - 100px);
        padding: 5px 15px;
        color: darken($color: $base-color, $amount: 15%);
        @media #{$xs_mobile} {
            width: 100%;
        }
    }
}
.note {
    color: $highlight-color;
}
.alert-box {
    background: lighten($color: $gray-light-color, $amount: 23%);
    padding: 15px 25px;
}
.highlight {
    color: $highlight-color;
    font-size: $font-xxs;
    &-light {
        color: darken($color: $highlight-color, $amount: 5%);
    }
}

//
.folding-table{
    th.show-first,td.show-first,th.show-second,td.show-second {
        display: none;
    }
    tr {
        td {
            &:after{
                display: none;
            }
        }
    }
} 

@media only screen and (max-width: 980px) {
    .folding-table,
    .folding-table thead,
    .folding-table tbody,
    .folding-table th,
    .folding-table tr,
    .folding-table td {
        display: block;
    }

    .folding-table th.hide-first,
    .folding-table td.hide-first,
    .folding-table th.hide-second,
    .folding-table td.hide-second {
        display: none;
    }

    .folding-table .expanded th.hide-first,
    .folding-table .expanded td.hide-first,
    .folding-table .expanded th.hide-second,
    .folding-table .expanded td.hide-second {
        display: block;
    }

    .folding-table .expanded th.show-first,
    .folding-table .expanded td.show-first,
    .folding-table .expanded th.show-second,
    .folding-table .expanded td.show-second {
        display: none;
    }

    .folding-table tr:hover:after,
    .folding-table tr:focus:after {
        display: block;
    }

    .folding-table {
        thead {
            left: -9999px;
            position: absolute;
            top: -9999px;
        }
        tr {
            cursor: pointer;
            position: relative;
            &:focus {
                outline: none;
            }
        }
        tr:after {
            content: "";
            width: 30px;
            height: 30px;
            background: url("../images/icon-down-arrow.svg") no-repeat center;
            background-size: 15px;
            opacity: 0.7;
            right: 15px;
            bottom: 15px;
            column-span: all;
            position: absolute;
        }
        tr.expanded {
            &:after {
                content: "";
                width: 30px;
                height: 30px;
                background: url("../images/icon-up-arrow.svg") no-repeat center;
                background-size: 15px;
                opacity: 0.7;
            }
            td {
                overflow: hidden;
                white-space: normal;
                word-break: break-word;
                &::after{
                    display: block;
                }
            }
        }
        td {
            &:before {
                content: attr(data-header);
            }
        }
    }
}
