.service_area{
    padding-top: 120px;
    padding-bottom: 120px;
    @media #{$mobile_device} {
        padding-bottom: 30px;
    }
    @media #{$tablet_device} {
        padding-bottom: 50px;
    }
}
.single_service{
    padding: 15px 35px;
    position: relative;
    display: flex;
    align-items: center;
    height: 100%;
    border: 1px solid $white-color;
    @include transition(.3s);
    @media #{$tab}{
        flex-wrap: wrap;
    }
    @media #{$large_mobile}{
        padding: 10px 20px;
    }
    @media #{$xs_mobile}{
        padding: 10px 10px 10px 20px;
    }
    .icon{
        width: 50px;
        @media #{$tab}{
            width: 40px;
            margin-bottom: 10px;
        }
        @media #{$large_mobile}{
            margin-bottom: 0;
        }
        @media #{$small_mobile}{
            margin-bottom: 0;
        }
        @media #{$xs_mobile}{
            width: 30px;
            margin-bottom: 0;
        }
    }
    .single_service_title{
        width: calc(100% - 70px);
        margin-bottom: 0;
        @media #{$tab}{
            width: 100%;
        }
        @media #{$large_mobile}{
            width: calc(100% - 70px);
        }
        @media #{$small_mobile}{
            width: calc(100% - 70px);
        }
        @media #{$xs_mobile}{
            width: calc(100% - 70px);
            font-size: $font-sm;
        }
    }
    h3{
        font-size: 24px;
        font-weight: 600;
        color: #2C2C2C;
        margin-top: 24px;
        padding-bottom: 12px;
        @media #{$tablet_device} {
            font-size: 22px;
        }
    }
    p{
        color: #727272;
        font-size: 16px;
        font-weight: 400;
        line-height: 30px;
        margin-bottom: 0;
    }
    &::before{
        content: '';
        display: block;
        width: 5px;
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        background: $steady-color;
    }
    &:hover{
        background: $white-color;
        border: 1px solid $steady-color;
    }

    &-color1{
        background: lighten($color: $base-color, $amount: 15%);
    }
    &-color2{
        background: $safe-color;
    }
    &-color3{
        background: $comfort-color;
    }
}